import Popup from "./Popup.js"

export default class PopupDeleteCard extends Popup {
    constructor(selector, button, confirmBtn, deleteFunc) {
        super(selector, button);
       // this._submitForm = submitForm;
        this._confirmBtn = document.querySelector(confirmBtn); //кнопка подтверждения
        this._deleteFunc = deleteFunc; //функция удаления
     //   this._card = card;
      //  this._cardId = card._id;
    }
    open() {
        super.open;
        //submitForm
    }
    setEventListeners() {
        super.setEventListeners;
        this._confirmBtn.addEventListener('click', this._deleteFunc());
    }
}   